// ** React Imports
import { Link } from 'react-router-dom'
import { Can } from '@src/utility/context/Can.js'
// ** Store & Actions
import { store } from '@store/store'
import { deleteTask } from '../store'

// ** Icons Imports
import { MoreVertical, Trash2, Eye, Edit } from 'react-feather'

// ** Reactstrap Imports
import { Badge, UncontrolledDropdown, DropdownToggle, DropdownMenu, DropdownItem, Col, UncontrolledTooltip } from 'reactstrap'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import moment from 'moment'

const MySwal = withReactContent(Swal)

const deletefn = (id) => {

  return MySwal.fire({
    title: 'Are you sure?',
    text: "You won't be able to revert this!",
    icon: 'warning',
    showCancelButton: true,
    confirmButtonText: 'Yes, delete it!',
    customClass: {
      confirmButton: 'btn btn-primary',
      cancelButton: 'btn btn-outline-danger ms-1'
    },
    buttonsStyling: false
  }).then(async (result) => {
    if (result.value) {
      await store.dispatch(deleteTask(id))
      MySwal.fire({
        icon: 'success',
        title: 'Deleted!',
        text: 'Task has been deleted.',
        customClass: {
          confirmButton: 'btn btn-success'
        }
      })
    }
  })
}

const statusObj = [
  '',
  'light-primary',
  'light-secondary',
  'light-success',
  '',
  '',
  'light-warning',
  '',
  'light-danger'
]

const priorityOptions = ['', 'Low', 'Medium', 'High']
const statusOptions = ['', 'To Do', 'In progress', 'Completed', 'On Hold', 'Cancelled', 'Sent to Review', 'Request Changes']

const dateFormat = (value) => {
  if (value.length === 13) {
    return moment(value, 'x').format("MMM DD, YYYY")
  } else {
    return moment.unix(value).format("MMM DD, YYYY")
  }
}

export const ExpandableTable = ({ data }) => {
  return (
    <div className='expandable-content p-2'>
      <p>
        <span className='fw-bold'>Comment:</span> {data.comment}
      </p>
    </div>
  )
}

const overdueCheck = (val) => {
  if (val !== null) {
    let enddate
    if (val.enddate.length === 13) {
      enddate = moment(val.enddate, 'x')
    } else {
      enddate = moment.unix(val.enddate)
    }
    const taskstatus = parseInt(val.taskstatus)
    return enddate.diff(moment(), 'days') < 0 && (taskstatus === 1 || taskstatus === 2 || taskstatus === 6 || taskstatus === 7)
  }
}

const renderAging = (val) => {
  let date
  if (val.length === 13) {
    date = moment().diff(moment(val, 'x'), 'days')
  } else {
    date = moment().diff(moment.unix(val), 'days')
  }
  return date > 0 ? `${date} Day's` : ''
}

